import React from 'react';

export default function IssueDetail() {
  if (issue){
    return (
    <div className="text-area">
      <h3>Description</h3>
      <pre>{description}</pre>
      <Toasts
        showing={toastVisible}
        onDismiss={this.dismissToast}
        type={toastType}
      >
        {toastMessage}
      </Toasts>
    </div>
    );
  }
  
  
}
